@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');
$text-color: #2b2d42;
$link-color: #2b2d42;
$link-hover-color: #e71d36;
$link-hover-filter: invert(33%) sepia(92%) saturate(7107%) hue-rotate(344deg) brightness(94%) contrast(93%);

$background-color: #edf2f4;
$footer-background: #e4e9eb;
$nav-background: #dfd7d8;

$border-gray: 2px solid gray;

$transform400: transform 400ms ease-in-out;
//text style
@mixin link {
    font-size: 26px;
    color: $link-color;
    font-weight: 500;
    line-height: 40px;
    text-decoration: none;
}

* {
    margin: 0;
    padding: 0;
    font-family: Quicksand;
}
html {
    height: 100%;
}
body {
    min-height: 100%;
    min-width: fit-content;
}
#root {
    height: 100%;
}
.App {
    min-height: 100vh;
}
main {
    padding-bottom: 64px;
    padding-top: 2.5rem;
}
h1,
h2,
h3,
h4 {
    margin: 0;
}
h1 {
    font-size: 2.3rem;
}
h2 {
    font-size: 1.8rem;
    line-height: 1.8;
}
main {
    h1, h2 {
        font-family: 'Poppins', sans-serif;
    }
}
ul {
    list-style: none;
}
body {
    background: $background-color;
}

.App {
    position: relative;
    margin: auto;
    margin-block: 0;
}

a {
    @include link;
    margin: 0 auto;
    text-align: center;
}

.contact-list > div {
    a {
        width: 300px;
        max-width: 90%;
        border: $border-gray;
        border-radius: 5px;
        display: block;
        padding: 10px 5px;
        margin-bottom: 10px;
        text-transform: capitalize;
        transition: all 200ms;
    }
    a:hover {
        box-shadow: 0 0.5em 0.5em -0.5em $link-color;
    }
    a:hover,
    a:focus {
        color: $link-hover-color;
        border-color: $text-color;
        transform: translateY(-0.05em);
    }
    .link {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        .icon,
        img {
            display: none;
            width: 32px;
            height: 32px;
            position: absolute;
            left: 5px;
        }
    }
}
@media (min-width: 250px) {
    .contact-list > div .link {
        .icon,
        img {
            display: block;
        }
    }
}
main {
    text-align: center;
}

.content {
    padding-block: 3rem;
}

.name {
    color: $link-hover-color;
}
u {
    text-decoration: underline $link-hover-color;
}

.contact-list {
    margin-bottom: 20px;
}
//footer
footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #e71d36;
    padding-block: 20px;
    text-align: center;
    background-color: $footer-background;
    font-size: 1.2rem;
}

//header
header {
    position: relative;
    background-color: $footer-background;
    height: 60px;
    z-index: 1;
    padding-inline: 20px;
}
.header-inner {
    height: 100%;
    max-height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    h1 {
        width: fit-content;
        margin: 0;
    }
}
main.darken::after {
    content: "";

    position: absolute;
    background-color: #00000055;
    display: block;
    width: 100vw;
    height: calc(100vh - 60px);
    top: 60px;
    bottom: 0;
}

nav {
    display: block;
    position: relative;
    background-color: $background-color;
    width: 350px;

    border-radius: 10px;
    border: $border-gray;
    margin-inline: auto;
    top: 85px;
    transform: translateY(-200%);
    transition: transform 300ms linear;
    z-index: 2;

    ul {
        padding-block: 20px;

        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
    }
    li {
        display: inline-block;
        text-align: center;
        flex-basis: 100%;
        padding-block: 8px;
    }
    a {
        padding-block: 8px;
        color: $link-color;
        text-transform: capitalize;
    }
    a:hover {
        color: $link-hover-color;
        transition: color 100ms linear;
    }
}

nav.active {
    transition: transform 200ms ease-in;
    transform: translateY(0);
}
.menu-button {
    box-sizing: content-box;
    display: block;
    height: 26px;
    width: 32px;

    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    cursor: pointer;
    transition: $transform400;
}
.line {
    display: block;
    height: 4px;
    width: 100%;
    border-radius: 10px;

    background-color: #2b2d42;
}
.line:first-child {
    transform-origin: 0% 0%;
    transition: $transform400;
}
.line:nth-child(2) {
    transition: transform 0.2s ease-in-out;
}
.line:last-child {
    transform-origin: 0% 100%;
    transition: $transform400;
}
.menu-button.active {
    transform: translateX(4px);
    transition: $transform400;
    .line:first-child {
        transform: rotate(45deg);
    }
    .line:nth-child(2) {
        transform: scaleY(0);
    }
    .line:last-child {
        transform: rotate(-45deg);
    }
}

@media (min-width: 570px) {
    main.darken::after {
        display: none;
    }
    header {
        height: 90px;
        display: flex;
    }

    nav {
        margin: 0;
        margin-left: auto;
        position: initial;
        width: auto;
        padding: 20px 10px;
        border-radius: 0;
        border: none;
        background: none;
        transform: translateY(0);

        ul {
            flex-wrap: nowrap;
            padding: 0;
        }
        li {
            flex-basis: auto;
            padding: 8px;
            margin-inline: 8px;
        }

        a {
            font-size: 18px;
            font-weight: bold;
            text-transform: uppercase;
            padding: 0;

            text-decoration: underline;
            text-decoration-thickness: 2px;
            text-underline-offset: 3px;
        }
        a:hover {
            border-bottom: none;
            text-decoration: none;
        }
    }
    .menu-button {
        display: none;
    }
}
